import axios from 'axios';

// Google Scholar
// API
const BACKEND_PROXY_URL = '/api/google-scholar';

//  axios 
const createApiInstance = () => {
  return axios.create({
    timeout: 30000, // 30
    headers: {
      'Content-Type': 'application/json',
    }
  });
};

/**
 * 
 * @param {string} query - 
 * @param {number} page - 0
 * @param {number} results - 
 * @param {Object} filters - 
 * @returns {Promise<Object>} - 
 */
export const searchPapers = async (query, page = 0, results = 10, filters = {}) => {
  try {
    const api = createApiInstance();
    
    const params = {
      query,
      page,
      results,
      language: 'en'
    };

    // 
    if (filters.as_ylo) params.as_ylo = filters.as_ylo;
    if (filters.as_yhi) params.as_yhi = filters.as_yhi;
    if (filters.language) params.language = filters.language;
    if (filters.as_sdt) params.as_sdt = filters.as_sdt;
    if (filters.safe) params.safe = filters.safe;

    console.log('Google Scholar :', params);

    const response = await api.get(BACKEND_PROXY_URL + '/search', { params });
    
    console.log('Google Scholar :', {
      query,
      page,
      resultsCount: response.data.scholar_results?.length || 0
    });

    return {
      papers: response.data.scholar_results || [],
      searchInfo: response.data.search_information || {},
      pagination: response.data.pagination || {},
      serpapi_pagination: response.data.serpapi_pagination || {},
      total: response.data.search_information?.total_results || null
    };

  } catch (error) {
    console.error('Google Scholar :', error.message);
    
    if (error.response) {
      const status = error.response.status;
      const data = error.response.data;
      
      if (status === 429) {
        throw new Error('API');
      } else if (status === 403) {
        throw new Error('API');
      } else if (status === 500 && data?.error?.includes('')) {
        throw new Error('API');
      } else {
        throw new Error(`: ${data?.error || error.message}`);
      }
    } else if (error.code === 'ECONNABORTED') {
      throw new Error('');
    } else {
      throw new Error(`: ${error.message}`);
    }
  }
};

/**
 * 
 * @param {string} paperId - ID
 * @returns {Promise<Object>} - 
 */
export const getCitationInfo = async (paperId) => {
  try {
    const api = createApiInstance();
    
    const params = {
      query: paperId,
      language: 'en'
    };

    console.log('Google Scholar:', paperId);

    const response = await api.get(BACKEND_PROXY_URL + '/cite', { params });
    
    console.log('Google Scholar');

    return {
      citationInfo: response.data.citations || [],
      searchInfo: response.data.search_information || {}
    };

  } catch (error) {
    console.error(':', error.message);
    throw new Error(`: ${error.message}`);
  }
};

/**
 * 
 * @param {string} clusterId - ID
 * @param {number} limit - 
 * @returns {Promise<Object>} - 
 */
export const getRelatedPapers = async (clusterId, limit = 10) => {
  try {
    const api = createApiInstance();
    
    const params = {
      cluster: clusterId,
      results: limit,
      language: 'en'
    };

    console.log('Google Scholar:', clusterId);

    const response = await api.get(BACKEND_PROXY_URL + '/related', { params });
    
    console.log('Google Scholar');

    return {
      papers: response.data.scholar_results || [],
      searchInfo: response.data.search_information || {}
    };

  } catch (error) {
    console.error(':', error.message);
    throw new Error(`: ${error.message}`);
  }
};

/**
 * 
 * @param {Array} queries - 
 * @param {Object} options - 
 * @returns {Promise<Array>} - 
 */
export const batchSearchPapers = async (queries, options = {}) => {
  const {
    page = 0,
    results = 10,
    filters = {},
    maxConcurrent = 2 // 
  } = options;
  
  // 
  const searchResults = [];
  for (let i = 0; i < queries.length; i += maxConcurrent) {
    const batch = queries.slice(i, i + maxConcurrent);
    
    const batchPromises = batch.map(query => 
      searchPapers(query, page, results, filters)
        .catch(error => {
          console.error(` - : ${query}:`, error.message);
          return { papers: [], error: error.message };
        })
    );
    
    const batchResults = await Promise.all(batchPromises);
    searchResults.push(...batchResults);
    
    // 
    if (i + maxConcurrent < queries.length) {
      await new Promise(resolve => setTimeout(resolve, 2000));
    }
  }
  
  return searchResults;
};

/**
 * 
 * @param {Object} searchParams - 
 * @returns {Promise<Object>} - 
 */
export const advancedSearch = async (searchParams) => {
  const {
    query = '',
    exactPhrase = '',
    anyWords = '',
    excludeWords = '',
    author = '',
    publication = '',
    yearLow = '',
    yearHigh = '',
    page = 0,
    results = 10
  } = searchParams;
  
  // 
  let advancedQuery = query;
  
  if (exactPhrase) {
    advancedQuery += ` "${exactPhrase}"`;
  }
  
  if (anyWords) {
    const words = anyWords.split(' ').filter(word => word.trim());
    if (words.length > 0) {
      advancedQuery += ` (${words.join(' OR ')})`;
    }
  }
  
  if (excludeWords) {
    const words = excludeWords.split(' ').filter(word => word.trim());
    words.forEach(word => {
      advancedQuery += ` -${word}`;
    });
  }
  
  if (author) {
    advancedQuery += ` author:"${author}"`;
  }
  
  const filters = {};
  if (yearLow) filters.as_ylo = yearLow;
  if (yearHigh) filters.as_yhi = yearHigh;
  if (publication) {
    // 
    advancedQuery += ` source:"${publication}"`;
  }
  
  return searchPapers(advancedQuery.trim(), page, results, filters);
};

