import axios from 'axios';
import apiLogger from '../utils/apiLogger';

//  AI API  - 
const AI_API_ENDPOINT = '/api/ai/chat';

// 
export const AI_MODELS = {
  GPT_4O_MINI: 'gpt-4o-mini',
  GPT_4O_MINI_2024: 'gpt-4o-mini-2024-07-18',
  DEEPSEEK_V3: 'deepseek-v3'
};

// 
const DEFAULT_MODEL = AI_MODELS.GPT_4O_MINI_2024;

// axios - 
const aiApi = axios.create({
  timeout: 60000, // 60AI
  headers: {
    'Content-Type': 'application/json',
  }
});

// 
aiApi.interceptors.request.use(
  (config) => {
    console.log(' AI API :', {
      url: config.url,
      method: config.method,
      data: config.data ? { model: config.data.model, messageCount: config.data.messages?.length } : 'no data'
    });
    return config;
  },
  (error) => {
    console.error(' AI API :', error);
    return Promise.reject(error);
  }
);

// 
aiApi.interceptors.response.use(
  (response) => {
    console.log(' AI API :', response.status, response.statusText);
    return response;
  },
  (error) => {
    console.error(' AI API :', {
      status: error.response?.status,
      statusText: error.response?.statusText,
      message: error.message,
      url: error.config?.url
    });
    return Promise.reject(error);
  }
);

/**
 * 
 * @param {Object} paper - 
 * @param {string} dimension -  ('research_purpose', 'research_methods', 'metrics', 'research_results')
 * @param {string} model - AIgpt-4o
 * @param {boolean} enableLogging - true
 * @returns {Promise<string>} - 
 */
export const analyzePaperDimension = async (paper, dimension, model = DEFAULT_MODEL, enableLogging = true) => {
  const startTime = Date.now();
  
  if (!paper || !paper.title) {
    throw new Error('');
  }

  // 
  if (!Object.values(AI_MODELS).includes(model)) {
    console.warn(`: ${model}: ${DEFAULT_MODEL}`);
    model = DEFAULT_MODEL;
  }

  // 
  let prompt = '';
  const paperInfo = {
    title: paper.title,
    abstract: paper.abstract || '',
    authors: (paper.authors || []).map(a => a.name).join(', '),
    year: paper.year || '',
    venue: paper.venue || '',
    fields: (paper.fieldsOfStudy || []).join(', ')
  };

  switch (dimension) {
    case 'research_purpose':
      prompt = `\n\n${paperInfo.title}\n${paperInfo.abstract}\n${paperInfo.authors}\n${paperInfo.year}\n${paperInfo.venue}\n${paperInfo.fields}\n\n50`;
      break;
    case 'research_methods':
      prompt = `\n\n${paperInfo.title}\n${paperInfo.abstract}\n${paperInfo.authors}\n${paperInfo.year}\n${paperInfo.venue}\n${paperInfo.fields}\n\n50`;
      break;
    case 'metrics':
      prompt = `\n\n${paperInfo.title}\n${paperInfo.abstract}\n${paperInfo.authors}\n${paperInfo.year}\n${paperInfo.venue}\n${paperInfo.fields}\n\n50`;
      break;
    case 'research_results':
      prompt = `\n\n${paperInfo.title}\n${paperInfo.abstract}\n${paperInfo.authors}\n${paperInfo.year}\n${paperInfo.venue}\n${paperInfo.fields}\n\n50`;
      break;
    default:
      throw new Error(`: ${dimension}`);
  }

  try {
    const response = await aiApi.post(AI_API_ENDPOINT, {
      model: model,
      messages: [
        {
          role: 'user',
          content: prompt
        }
      ],
      stream: false,
      max_tokens: 100 // 
    });

    if (response.data && response.data.choices && response.data.choices.length > 0) {
      const result = response.data.choices[0].message.content;
      
      // 
      if (enableLogging) {
        const responseTime = Date.now() - startTime;
        apiLogger.logApiCall(
          'analyzePaperDimension',
          'ai',
          true,
          responseTime,
          null,
          {
            paperId: paper.paperId,
            paperTitle: paper.title,
            dimension,
            model,
            resultLength: result.length
          }
        );
      }
      
      return result;
    } else {
      console.error('AI API:', response.data);
      throw new Error('AI');
    }
  } catch (error) {
    // 
    if (enableLogging) {
      const responseTime = Date.now() - startTime;
      apiLogger.logApiCall(
        'analyzePaperDimension',
        'ai',
        false,
        responseTime,
        error.message,
        {
          paperId: paper.paperId,
          paperTitle: paper.title,
          dimension,
          model
        }
      );
    }
    
    console.error(`AI (: ${model}, : ${dimension}):`, error);
    if (error.response) {
      console.error(':', error.response.status);
      console.error(':', error.response.data);
    }
    throw new Error(`AI: ${error.message}`);
  }
};

/**
 * 
 * @param {Array} papers - 
 * @param {Array} dimensions - 
 * @param {Function} onProgress - 
 * @param {string} model - AIgpt-4o
 * @returns {Promise<void>}
 */
export const batchAnalyzePapers = async (papers, dimensions, onProgress, model = DEFAULT_MODEL) => {
  if (!papers || papers.length === 0) {
    throw new Error('');
  }

  if (!dimensions || dimensions.length === 0) {
    throw new Error('');
  }

  // 
  if (!Object.values(AI_MODELS).includes(model)) {
    console.warn(`: ${model}: ${DEFAULT_MODEL}`);
    model = DEFAULT_MODEL;
  }

  // 
  const batchStats = {
    startTime: Date.now(),
    totalTasks: papers.length * dimensions.length,
    successCount: 0,
    failedCount: 0,
    errors: []
  };

  // 
  let isCancelled = false;

  // 
  batchAnalyzePapers.cancel = () => {
    isCancelled = true;
  };

  // 
  const analysisQueue = [];
  let completed = 0;
  const totalTasks = papers.length * dimensions.length;

  // 
  for (let paperIndex = 0; paperIndex < papers.length; paperIndex++) {
    const paper = papers[paperIndex];
    
    for (let dimensionIndex = 0; dimensionIndex < dimensions.length; dimensionIndex++) {
      const dimension = dimensions[dimensionIndex];
      
      // 
      analysisQueue.push({
        paperIndex,
        dimensionIndex,
        paper,
        dimension,
        // 
        status: 'pending'
      });
    }
  }

  try {
    // Promise.all
    const concurrencyLimit = 3; // 3
    let activeTasks = 0;
    let queueIndex = 0;

    // 
    while (queueIndex < analysisQueue.length || activeTasks > 0) {
      // 
      if (isCancelled) {
        break;
      }

      // 
      while (queueIndex < analysisQueue.length && activeTasks < concurrencyLimit) {
        const task = analysisQueue[queueIndex];
        if (task.status === 'pending') {
          task.status = 'running';
          activeTasks++;
          
          // 
          (async (currentTask) => {
            let taskStartTime = Date.now();
            try {
              // 
              if (isCancelled) return;
              
              const result = await analyzePaperDimension(currentTask.paper, currentTask.dimension, model, false); // false
              
              // 
              if (isCancelled) return;
              
              // 
              if (onProgress) {
                onProgress(currentTask.paperIndex, currentTask.dimensionIndex, {
                  paperId: currentTask.paper.paperId,
                  dimension: currentTask.dimension,
                  result: result,
                  model: model
                });
              }
              
              currentTask.status = 'completed';
              batchStats.successCount++;
              completed++;
      
            } catch (error) {
              console.error(` ${currentTask.paper.title}  ${currentTask.dimension} :`, error);
              
              // 
              batchStats.errors.push({
                paper: currentTask.paper.title,
                dimension: currentTask.dimension,
                error: error.message,
                responseTime: Date.now() - taskStartTime
              });
              
              // 
              if (onProgress) {
                onProgress(currentTask.paperIndex, currentTask.dimensionIndex, {
                  paperId: currentTask.paper.paperId,
                  dimension: currentTask.dimension,
                  result: `: ${error.message}`,
                  model: model
                });
              }
              
              currentTask.status = 'failed';
              batchStats.failedCount++;
              completed++;
            } finally {
              // 
              activeTasks--;
            }
          })(task);
        }
        
        queueIndex++;
      }
      
      // CPU
      await new Promise(resolve => setTimeout(resolve, 100));
    }
    
    // 
    const totalTime = Date.now() - batchStats.startTime;
    const averageResponseTime = Math.round(totalTime / batchStats.totalTasks);
    
    apiLogger.logBatchOperation(
      'analyzePaperDimension',
      batchStats.totalTasks,
      batchStats.successCount,
      batchStats.failedCount,
      averageResponseTime,
      batchStats.errors.map(e => `${e.paper}(${e.dimension}): ${e.error}`)
    );
    
  } finally {
    // 
    batchAnalyzePapers.cancel = null;
  }
};

// 
batchAnalyzePapers.cancel = null; 

