/**
 *  - 
 * @param {Array} papers - 
 * @param {string} model - AIgpt-4o
 * @param {Function} onProgress - 
 * @returns {Promise<string>} - 
 */
export const generateResearchReport = async (papers, model = DEFAULT_MODEL, onProgress = null) => {
  if (!papers || papers.length === 0) {
    throw new Error('');
  }

  // 
  if (!Object.values(AI_MODELS).includes(model)) {
    console.warn(`: ${model}: ${DEFAULT_MODEL}`);
    model = DEFAULT_MODEL;
  }

  //  APA 
  const papersSummary = papers.map((paper, index) => {
    const authors = (paper.authors || []).map(a => a.name).join(', ');
    const year = paper.year || 'n.d.';
    const venue = paper.venue || 'Unknown venue';
    
    //  APA 
    const apaReference = `${authors} (${year}). ${paper.title}. ${venue}.`;
    
    return `[${index + 1}] ${apaReference}
   ${(paper.fieldsOfStudy || []).join(', ')}
   ${paper.abstract || ''}
   ${paper.citationCount || 0}`;
  }).join('\n\n');

  const prompt = ` APA 


${papersSummary}



1. ****
   - 
   -  APA (, )   () ...
   - 
   - 

2. ****
   -  [1], [2] 
   - [1, 3, 5]  [1-3]
   - [1, p. 15] ()

3. ****

## 
150-200

## 

- 
- 
- 
- 

## 

- 
- 
- 
- 

## 

- 
- 
- 
- 

## 

- 
- 
- 
- 

## 
 APA 


- 1200-1800
- 
- 
- 
- `;

  try {
    
    // fetch APIaxios
    const response = await fetch(AI_API_ENDPOINT, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: model,
        messages: [
          {
            role: 'system',
            content: '1) 2)  APA 3) 4) 5) '
          },
          {
            role: 'user',
            content: prompt
          }
        ],
        stream: true,
        max_tokens: 2500
      })
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let fullReport = '';

    try {
      while (true) {
        const { done, value } = await reader.read();
        
        if (done) {
          break;
        }

        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n');

        for (const line of lines) {
          if (line.trim() === '') continue;
          if (line.trim() === 'data: [DONE]') {
            return fullReport;
          }

          if (line.startsWith('data: ')) {
            const jsonStr = line.substring(6);
            try {
              const data = JSON.parse(jsonStr);
              if (data.choices && data.choices[0] && data.choices[0].delta && data.choices[0].delta.content) {
                const content = data.choices[0].delta.content;
                fullReport += content;

                // UI
                if (onProgress) {
                  onProgress(content, fullReport);
                }
              }
            } catch (parseError) {
              console.warn(':', parseError);
            }
          }
        }
      }

      return fullReport;
    } finally {
      reader.releaseLock();
    }
    
  } catch (error) {
    console.error(`AI (: ${model}):`, error);
    throw new Error(`AI: ${error.message}`);
  }
}; 

/**
 * AI
 * @param {Object} paper - 
 * @param {string} model - AIgpt-4o-mini
 * @returns {Promise<Object>} - 
 */
export const translatePaperContent = async (paper, model = DEFAULT_MODEL) => {
  if (!paper || !paper.title) {
    throw new Error('');
  }

  // 
  if (!Object.values(AI_MODELS).includes(model)) {
    console.warn(`: ${model}: ${DEFAULT_MODEL}`);
    model = DEFAULT_MODEL;
  }

  // 
  let contentToTranslate = `${paper.title}`;
  
  if (paper.abstract && paper.abstract.trim()) {
    contentToTranslate += `\n\n${paper.abstract}`;
  }
  
  if (paper.tldr && paper.tldr.text && paper.tldr.text.trim()) {
    contentToTranslate += `\n\nAI${paper.tldr.text}`;
  }

  const prompt = `
1. 
2. 
3. 
4. 
5. 

[]
[]
AI[AI]

""


${contentToTranslate}`;

  try {
    
  
    
    const response = await aiApi.post(AI_API_ENDPOINT, {
      model: model,
      messages: [
        {
          role: 'system',
          content: ''
        },
        {
          role: 'user',
          content: prompt
        }
      ],
      stream: false,
      max_tokens: 1000 // token
    });

    if (response.data && response.data.choices && response.data.choices.length > 0) {
      const translatedContent = response.data.choices[0].message.content.trim();
      
      // 
      const result = {
        originalTitle: paper.title,
        translatedTitle: null,
        originalAbstract: paper.abstract || null,
        translatedAbstract: null,
        originalSnippet: paper.tldr?.text || null,
        translatedSnippet: null,
        model: model
      };
      
      // 
      const titleMatch = translatedContent.match(/(.+?)(?=\n|$)/);
      if (titleMatch) {
        result.translatedTitle = titleMatch[1].trim();
      }
      
      const abstractMatch = translatedContent.match(/(.+?)(?=\nAI|$)/s);
      if (abstractMatch && abstractMatch[1].trim() !== '') {
        result.translatedAbstract = abstractMatch[1].trim();
      }
      
      const snippetMatch = translatedContent.match(/AI(.+?)$/s);
      if (snippetMatch && snippetMatch[1].trim() !== '') {
        result.translatedSnippet = snippetMatch[1].trim();
      }
      
      return result;
    } else {
      console.error('AI API:', response.data);
      throw new Error('AI');
    }
  } catch (error) {
    console.error(`AI (: ${model}):`, error);
    if (error.response) {
      console.error(':', error.response.status);
      console.error(':', error.response.data);
    }
    throw new Error(`AI: ${error.message}`);
  }
};

