import axios from 'axios';

// IP
const geoCache = new Map();

// IP
class IPGeoService {
  constructor() {
    this.cache = geoCache;
    this.requestQueue = new Map(); // 
  }

  // IP
  async getIPLocation(ip) {
    // 
    if (this.cache.has(ip)) {
      return this.cache.get(ip);
    }

    // 
    if (this.requestQueue.has(ip)) {
      return this.requestQueue.get(ip);
    }

    // IP
    if (!ip || ip === 'unknown' || ip === 'local_user') {
      const result = {
        country: '',
        region: '',
        city: '',
        displayText: '',
        flag: ''
      };
      this.cache.set(ip, result);
      return result;
    }

    // IP
    if (this.isLocalIP(ip)) {
      const result = {
        country: '',
        region: '',
        city: '',
        displayText: '',
        flag: ''
      };
      this.cache.set(ip, result);
      return result;
    }

    // Promise
    const requestPromise = this.fetchIPLocation(ip);
    this.requestQueue.set(ip, requestPromise);

    try {
      const result = await requestPromise;
      this.cache.set(ip, result);
      return result;
    } catch (error) {
      console.warn('IP:', ip, error.message);
      const fallbackResult = {
        country: '',
        region: '',
        city: '',
        displayText: '',
        flag: ''
      };
      this.cache.set(ip, fallbackResult);
      return fallbackResult;
    } finally {
      this.requestQueue.delete(ip);
    }
  }

  // IP
  async fetchIPLocation(ip) {
    // CORSAPI
    const apis = [
      // API 1: ipapi.co (HTTPSCORS)
      {
        url: `https://ipapi.co/${ip}/json/`,
        parseResponse: (data) => ({
          country: data.country_name || '',
          region: data.region || '',
          city: data.city || '',
          displayText: this.formatDisplayText(data.country_name, data.region, data.city),
          flag: this.getCountryFlag(data.country_code)
        })
      },
      // API 2: ip.sb (CORS)
      {
        url: `https://api.ip.sb/geoip/${ip}`,
        parseResponse: (data) => ({
          country: data.country || '',
          region: data.region || '',
          city: data.city || '',
          displayText: this.formatDisplayText(data.country, data.region, data.city),
          flag: this.getCountryFlag(data.country_code)
        })
      },
      // API 3: ipgeolocation.io (CORS)
      {
        url: `https://api.ipgeolocation.io/ipgeo?apiKey=free&ip=${ip}`,
        parseResponse: (data) => ({
          country: data.country_name || '',
          region: data.state_prov || '',
          city: data.city || '',
          displayText: this.formatDisplayText(data.country_name, data.state_prov, data.city),
          flag: this.getCountryFlag(data.country_code2)
        })
      }
    ];

    for (let i = 0; i < apis.length; i++) {
      const api = apis[i];
      try {
        const response = await axios.get(api.url, {
          timeout: 5000,
          headers: {
            'Accept': 'application/json'
          }
        });

        const result = api.parseResponse(response.data);
        console.log(`IP (API ${i + 1}):`, ip, result.displayText);
        return result;
      } catch (error) {
        console.warn(`IPAPI ${i + 1}:`, error.message);
        if (i === apis.length - 1) {
          throw new Error('IPAPI');
        }
      }
    }
  }

  // 
  formatDisplayText(country, region, city) {
    const parts = [];
    
    if (city && city !== region) parts.push(city);
    if (region && region !== country) parts.push(region);
    if (country) parts.push(country);
    
    return parts.length > 0 ? parts.join(', ') : '';
  }

  // emoji
  getCountryFlag(countryCode) {
    if (!countryCode || countryCode.length !== 2) return '';
    
    const flagMap = {
      'CN': '', 'US': '', 'JP': '', 'KR': '',
      'GB': '', 'DE': '', 'FR': '', 'CA': '',
      'AU': '', 'RU': '', 'IN': '', 'SG': '',
      'HK': '', 'TW': '', 'MY': '', 'TH': ''
    };

    return flagMap[countryCode.toUpperCase()] || this.convertToFlag(countryCode);
  }

  // emoji
  convertToFlag(countryCode) {
    if (!countryCode || countryCode.length !== 2) return '';
    
    try {
      const codePoints = countryCode
        .toUpperCase()
        .split('')
        .map(char => 127397 + char.charCodeAt(0));
      return String.fromCodePoint(...codePoints);
    } catch (error) {
      return '';
    }
  }

  // IP
  isLocalIP(ip) {
    if (!ip) return false;
    
    const localPatterns = [
      /^127\./, // 127.x.x.x
      /^192\.168\./, // 192.168.x.x
      /^10\./, // 10.x.x.x
      /^172\.(1[6-9]|2[0-9]|3[0-1])\./, // 172.16.x.x - 172.31.x.x
      /^::1$/, // IPv6 localhost
      /^fe80:/, // IPv6 link-local
      /^localhost$/i
    ];

    return localPatterns.some(pattern => pattern.test(ip));
  }

  // 
  clearCache() {
    this.cache.clear();
  }

  // 
  getCacheStats() {
    return {
      size: this.cache.size,
      entries: Array.from(this.cache.entries())
    };
  }

  // IP
  async preloadIPs(ips) {
    const uniqueIPs = [...new Set(ips.filter(ip => ip && !this.cache.has(ip)))];
    
    if (uniqueIPs.length === 0) return;

    console.log(`${uniqueIPs.length}IP...`);
    
    // 
    const batchSize = 5;
    for (let i = 0; i < uniqueIPs.length; i += batchSize) {
      const batch = uniqueIPs.slice(i, i + batchSize);
      await Promise.allSettled(
        batch.map(ip => this.getIPLocation(ip))
      );
      
      // API
      if (i + batchSize < uniqueIPs.length) {
        await new Promise(resolve => setTimeout(resolve, 200));
      }
    }
    
    console.log('IP');
  }
}

// 
const ipGeoService = new IPGeoService();

export default ipGeoService; 