const ToggleButton = styled.button`
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  background: ${props => props.$active ? '#e6f7ff' : 'white'};
  color: ${props => props.$active ? '#1890ff' : '#666'};
  border: 1px solid ${props => props.$active ? '#1890ff' : '#e0e0e0'};
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  
  /*  -  */
  @media (max-width: 1600px) {
    /* 1600px */
    svg {
      display: none;
    }
    gap: 4px;
  }
  
  @media (max-width: 992px) {
    padding: 6px 8px;
    font-size: 13px;
    gap: 4px;
  }
  
  @media (max-width: 768px) {
    padding: 4px 6px;
    font-size: 12px;
    
    /*  */
    span {
      display: none;
    }
    /*  */
    svg {
      display: block !important;
    }
  }
  
  /*  */
  transition: 
    all 0.3s cubic-bezier(0.4, 0, 0.2, 1),
    transform 0.2s ease,
    box-shadow 0.3s ease;
  
  /*  */
  transform: translateY(0) scale(1);
  
  /*  */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.3),
      transparent
    );
    transition: left 0.5s ease;
  }
  
  /*  */
  &::after {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(45deg, 
      rgba(24, 144, 255, 0.2), 
      rgba(64, 169, 255, 0.2), 
      rgba(24, 144, 255, 0.2)
    );
    border-radius: 6px;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: -1;
  }
  
  &:hover {
    background: ${props => props.$active ? '#e6f7ff' : '#f0f8ff'};
    border-color: ${props => props.$active ? '#1890ff' : '#1890ff'};
    color: ${props => props.$active ? '#1890ff' : '#1890ff'};
    transform: translateY(-1px) scale(1.02);
    box-shadow: 0 4px 12px rgba(24, 144, 255, 0.25);
    
    /*  */
    &::before {
      left: 100%;
    }
    
    /*  */
    &::after {
      opacity: 1;
      animation: highlight-glow 2s ease-in-out infinite;
    }
  }
  
  &:active {
    transform: translateY(0) scale(0.98);
    transition: transform 0.1s ease;
  }
  
  /*  */
  ${props => props.$active && `
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
    
    &:hover {
      box-shadow: 
        0 0 0 2px rgba(24, 144, 255, 0.3),
        0 4px 12px rgba(24, 144, 255, 0.3);
    }
  `}
  
  @keyframes highlight-glow {
    0%, 100% {
      background: linear-gradient(45deg, 
        rgba(24, 144, 255, 0.1), 
        rgba(64, 169, 255, 0.1), 
        rgba(24, 144, 255, 0.1)
      );
    }
    50% {
      background: linear-gradient(45deg, 
        rgba(24, 144, 255, 0.3), 
        rgba(64, 169, 255, 0.3), 
        rgba(24, 144, 255, 0.3)
      );
    }
  }
`;

