import toast from 'react-hot-toast';

/**
 * Toast - 
 */
class ToastUtils {
  
  // 
  static defaultConfig = {
    duration: 4000,
    position: 'top-right',
    style: {
      borderRadius: '8px',
      background: '#333',
      color: '#fff',
      fontSize: '14px',
      fontFamily: 'system-ui, -apple-system, sans-serif',
      boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
      maxWidth: '420px',
    },
  };

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static success(message, options = {}) {
    return toast.success(message, {
      ...this.defaultConfig,
      style: {
        ...this.defaultConfig.style,
        background: '#10b981',
        color: '#fff',
      },
      iconTheme: {
        primary: '#10b981',
        secondary: '#fff',
      },
      ...options
    });
  }

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static error(message, options = {}) {
    return toast.error(message, {
      ...this.defaultConfig,
      duration: 6000, // 
      style: {
        ...this.defaultConfig.style,
        background: '#ef4444',
        color: '#fff',
      },
      iconTheme: {
        primary: '#ef4444',
        secondary: '#fff',
      },
      ...options
    });
  }

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static warning(message, options = {}) {
    return toast(message, {
      ...this.defaultConfig,
      icon: '',
      style: {
        ...this.defaultConfig.style,
        background: '#f59e0b',
        color: '#fff',
      },
      ...options
    });
  }

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static info(message, options = {}) {
    return toast(message, {
      ...this.defaultConfig,
      icon: '',
      style: {
        ...this.defaultConfig.style,
        background: '#3b82f6',
        color: '#fff',
      },
      ...options
    });
  }

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static loading(message, options = {}) {
    return toast.loading(message, {
      ...this.defaultConfig,
      style: {
        ...this.defaultConfig.style,
        background: '#6b7280',
        color: '#fff',
      },
      ...options
    });
  }

  /**
   * 
   * @param {string} dataSource - 
   * @param {string} operation - 
   * @param {string} errorDetails - 
   */
  static networkError(dataSource, operation = '', errorDetails = '') {
    const sourceNames = {
      'primaryScraping': 'Primary Scraping',
      'googleScholar': 'Google Scholar',
      'semantic': 'Semantic Scholar'
    };
    
    const sourceName = sourceNames[dataSource] || dataSource;
    let message = `${sourceName} ${operation}`;
    
    if (errorDetails.includes('timeout') || errorDetails.includes('')) {
      message = `${sourceName} `;
    } else if (errorDetails.includes('ENOTFOUND') || errorDetails.includes('')) {
      message = ` ${sourceName}`;
    } else if (errorDetails.includes('429')) {
      message = `${sourceName} `;
    } else if (errorDetails.includes('403')) {
      message = `${sourceName} API`;
    } else if (errorDetails.includes('500') || errorDetails.includes('502') || errorDetails.includes('503')) {
      message = `${sourceName} `;
    } else if (errorDetails) {
      message = `${sourceName} ${operation}: ${errorDetails}`;
    }
    
    return this.error(message, {
      duration: 8000, // 
    });
  }

  /**
   * API
   * @param {string} dataSource - 
   * @param {string} operation - 
   * @param {Error} error - 
   */
  static apiError(dataSource, operation = '', error) {
    console.error(`${dataSource} ${operation} API:`, error);
    
    // 
    let errorMessage = error.message || error.toString();
    
    // 
    if (errorMessage.includes('ERR_PROXY_CONNECTION_FAILED')) {
      errorMessage = '';
    } else if (errorMessage.includes('ECONNREFUSED')) {
      errorMessage = '';
    } else if (errorMessage.includes('ETIMEDOUT')) {
      errorMessage = '';
    }
    
    return this.networkError(dataSource, operation, errorMessage);
  }

  /**
   * 
   * @param {string} currentSource - 
   * @param {Array} availableSources - 
   */
  static suggestAlternativeSource(currentSource, availableSources = []) {
    const sourceNames = {
      'primaryScraping': 'Primary Scraping',
      'googleScholar': 'Google Scholar',
      'semantic': 'Semantic Scholar'
    };
    
    const currentSourceName = sourceNames[currentSource] || currentSource;
    
    // 
    const hasAlternatives = availableSources.filter(source => source !== currentSource).length > 0;
    
    if (hasAlternatives) {
      return this.info(
        `${currentSourceName} `,
        { duration: 6000 }
      );
    }
  }

  /**
   * 
   * @param {string} message - 
   * @param {object} options - 
   */
  static custom(message, options = {}) {
    return toast(message, {
      ...this.defaultConfig,
      ...options
    });
  }

  /**
   * toast
   */
  static dismiss() {
    toast.dismiss();
  }

  /**
   * toast
   * @param {string} toastId - toast ID
   */
  static dismissById(toastId) {
    toast.dismiss(toastId);
  }
}

export default ToastUtils; 